.sp-links-box {
  display: inline-flex;
  flex-direction: column;
  justify-content: flex-start;
  /* flex-wrap: wrap; */
  padding: 1em 1em 0;
  @include breakpoint($pc) {
    padding: 0;
    flex-direction: row;
  }
}

.sp-link {
  margin-bottom: 1em;
  padding: .8em 2.167em;
  background-color: #c52525;
  color: #fff;
  font-size: em(24);
  font-weight: bold;
  &:last-child {
    margin-bottom: auto;
  }
  @include breakpoint($pad) {
    margin-bottom: 0;
    margin-right: .3em;
    padding: .667em .778em;
    font-size: em(18);
    color: $black;
    background-color: transparent;
    &:hover {
      background-color: #c52525;
      color: $white;
    }
  }
  &.js--bg-pad {
    @include breakpoint($pad) {
      color: #fff;
      background-color: #c52525;
    }
  }
}
