.content__inside {
  padding-top: em(19);
  letter-spacing: .1em;
  @include breakpoint($pc) {
    padding-top: 0;
  }
  & > * {
    margin-bottom: em(19);
  }
  h2 {
    margin: 0 auto .3166em;
    font-size: em(60);
    /* line-height: 1.4em; */
  }
  img {
    display: block;
    width: 100%;
    height: auto;
  }
  p {
    margin-bottom: 2em;
    display: block;
    line-height: 1.8em;
    a {
      display: inline-block;
      color: $red;
      text-decoration: none;
      &.underline {
        border-bottom: $red solid 1px;
      }
      &.strong {
        font-size: 1.6em;
        font-weight: bold;
      }
    }
  }
  & > *:last-child {
    margin-bottom: 0;
  }
}

@import "right-ad";
