.scroll {
  @include mid;
  margin-top: 25px;
  margin-bottom: 25px;
  display: table;
  width: 100%;
  text-align: center;
  color: rgb(30, 30, 30);
  cursor: pointer;
  p {
    display: table-cell;
    padding: 0 em(5) 25px;
    line-height: 1.2em;
    white-space: nowrap;
    background: transparent url(../images/layout/scroll_down.png) 50% 100% no-repeat;
    font-size: 18px;
  }
  &:before, &:after {
    $t0: 25px;
    $t1: $t0 + 1;
    content: '';
    display: table-cell;
    width: 50%;
    opacity: .2;
    background-image: linear-gradient(transparent $t0, currentColor $t0, currentColor $t1, transparent $t1);
    background-clip: padding-box;
  }
}
