.content-nav {
  font-size: em(36);
  @include breakpoint($pc) {
    font-size: em(18);
    width: 180px;
  }
}

@mixin content-nav-node {
  width: 100%;
  height: auto;
  padding: em(7) em(35) em(7) em(7);
  border: rgb(222, 222, 222) solid 1px;
  border-radius: 5px;
  background-color: rgb(240, 240, 240);
  cursor: pointer;
  @include breakpoint($pc) {
    padding: 0;
    border: 0;
    background-color: transparent;
  }
}
.content-nav__result {
  @include content-nav-node;
  overflow: hidden;
  display: block;
  background-image: url(../images/layout/arrow_down.png);
  background-repeat: no-repeat;
  background-position: right em(9) top 50%;
  &.js--opened {
    background-image: url(../images/layout/arrow_top.png);
  }
  span {
    overflow: hidden;
    display: block;
    width: 100%;
    height: 1.2em;
    line-height: 1.2em;
  }
  @include breakpoint($pc) {
    display: none;
  }
}

.content-nav__nodes {
  display: none;
  &.js--opened {
    display: block;
  }
  @include breakpoint($pc) {
    display: block;
  }
  li {
    margin-top: em(7);
    @include content-nav-node;
    display: block;
    &.ul__legend {
      display: none;
    }
    @include breakpoint($pc) {
      position: relative;
      margin-top: 1em;
      padding-left: 1em;
      line-height: 1.4em;
      border-radius: 0;
      &:before {
        content: '';
        position: absolute;
        top: .5em;
        left: .2em;
        display: block;
        width: 10px;
        height: 10px;
        background-color: #c52525;
        border-radius: 100%;
      }
      &.ul__legend {
        margin-top: 1.5em;
        display: block;
        padding-top: 1.5em;
        padding-left: 0;
        border-top: #bebebe solid 1px;
        font-weight: bold;
        &:before {
          display: none;
        }
        &:first-child {
          margin-top: 0;
          padding-top: 0;
          border-top: 0;
        }
      }
    }
  }
}

// animation
.content-nav-node-enter,
.content-nav-node-enter-active {
  z-index: 2;
}
.content-nav-node-enter {
  opacity: 0;
  transform: translateY(-5%);
}
.content-nav-node-enter-active {
  transition: all .6s ease;
}
