.right-ad {
  position: fixed;
  right: 75px;
  bottom: 2px;
  @include breakpoint($large_screen) {
    right: auto;
    left: 50%;
    margin-left: 550px;
  }
}

.go-top {
    cursor: pointer;
    position: fixed;
    right: 2px;
    bottom: 2px;
    display: block;
    width: 60px;
    height: 60px;
    background-repeat: no-repeat;
    background-image: url(../images/upload/gotop.png);
}
