.strong {
  font-weight: bold;
  @include breakpoint($pc) {
    font-size: 1.1em;
  }
}

.container {
  position: relative;
  margin: 0 auto;
  width: 100%;
  min-height: 100vh;
  padding-bottom: 220px;
  max-width: $desktop;
  background-color: $white;
  @include breakpoint($large_screen) {
    padding-bottom: 0;
  }
}

.content-wrap {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  max-width: $desktop;
  padding: 0 em(16) em(75);
  @include breakpoint($pc) {
    /* margin-top: 50px; */
    flex-direction: row;
  }
}

.wrap__whole {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.wrap__start {
  @include breakpoint($pc) {
    padding-top: 50px;
  }
}

.wrap__end {
  @include breakpoint($pc) {
    padding-top: 50px;
    padding-left: 3em;
  }
}


.footer {
  display: block;
  padding: em(30) em(16);
  border-top: #e6e6e6 solid 1px;
  line-height: 1.8em;
  color: #646464;
  p a {
    margin-left: .5em;
  }
  @include breakpoint($pad) {
    p {
      display: inline-block;
    }
  }
  @include breakpoint($pc) {
    font-size: em(14);
  }
}

.footer__nav {

}

.footer__nav__node {
  display: block;
  @include breakpoint($pad) {
    display: inline-block;
    &:after {
      content: "|";
      margin: 0 .5em;
    }
    &:last-child {
      &:after {
        display: none;
      }
    }
  }
}

.footer__copyright {
  span {
    display: block;
    @include breakpoint($pad) {
      display: inline-block;
    }
  }
}
