$active-time: 1.2s;
$leave-time: .2s;

.shoow-box-enter,
.shoow-box-enter-active {
}
.shoow-box-enter {
  opacity: 0;
  transform: translateX( 10% );
  &.last {
    transform: translateX( -10% );
  }
}
.shoow-box-enter-active {
  transition: all $active-time;
  transition-delay: $leave-time;
}

.shoow-box-leave-active {
  transition: all $leave-time;
  transform: translateX( -100% );
  &.last {
    transform: translateX( 100% );
  }
}

.slider-box {
  position: relative;
  margin-bottom: 40px;
  display: block;
  width: 100%;
  color: $white;
  font-size: em(18);
  line-height: 1.2em;
  text-shadow: 1px 1.732px 5px rgba(0, 0, 0, 0.75);
  @include breakpoint($pad) {
    font-size: em(24)
  }
  h3 {
    max-width: 7em;
    font-size: em(24);
    line-height: 1.2em;
    font-weight: bold;
    @include breakpoint($pad) {
      font-size: 48px;
      max-width: 100%;
    }
  }
  p {
    margin-top: .3em;
  }
  .button {
    margin-top: .8em;
    display: none;
    padding: 6px 18px;
    font-size: 16px;
    background-color: #c52525;
    text-shadow: none;
    transition: all .3s;
    @include breakpoint($pad) {
      display: block;
    }
  }
}

.slider__wrap {
  position: relative;
  overflow: hidden;
  display: block;
  width: 100%;
  padding-top: 500 / 1080 * 100%;
}

.slider__contents {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
}

.slider__content {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  width: 100%;
  height: 100%;
  padding: 15px 49px;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  /* background-image: url(../images/upload/banner.jpg); */
  background-size: cover;
  @include breakpoint($pad) {
    padding: 35px 63px;
  }
  @include breakpoint($pc) {
    padding: 50px 91px;
  }
  .contents {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-end;
    width: 100%;
    height: 100%;
  }
}

.slider-box__img {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  min-height: 100%;
}

.slider__link {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  @include hide-text;
}

.slider__action {
  position: absolute;
  top: 50%;
  margin-top: 40px * -.5;
  display: block;
  width: 40px;
  height: 40px;
  background-color: $white;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  transition: all .3s;
  @include hide-text;
  &:hover {
    background-color: rgba($white, .6);
  }
  @include breakpoint($pad) {
    margin-top: 48px * -.5;
    width: 48px;
    height: 48px;
  }
  @include breakpoint($pc) {
    margin-top: 60px * -.5;
    width: 60px;
    height: 60px;
  }
}

.slider__last {
  left: 0;
  background-image: url(../images/layout/arrow_last.png);
}

.slider__next {
  right: 0;
  background-image: url(../images/layout/arrow_next.png);
}
