.index__wrap {
  position: relative;
  /* margin-top: 40px; */
  width: 100%;
  h2 {
    font-size: 60px;
    line-height: 1.2em;
    margin-bottom: 18px;
  }
  img {
    display: block;
    width: 100%;
    height: auto;
  }
  strong {
    color: rgb(13, 95, 146);
  }
  p {
    margin-bottom: 14px;
    line-height: 1.66em;
    &:last-child {
      margin-bottom: 0;
    }
  }
  .box-link {
    margin-top: 40px;
    margin-bottom: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    a {
      display: block;
      padding: 18px 68px;
      color: $white;
      background-color: #c52525;
    }
  }
}

@mixin index_a {
  padding-bottom: 1px;
  margin-top: 20px;
  color: rgb(197, 37, 37);
  font-size: 24px;
  line-height: 1.2em;
  text-decoration: underline;
}

.p0 {
  img {
    display: block;
    width: 100%;
    height: auto;
  }
  h3 {
    margin-top: 20px;
    font-size: 30px;
    line-height: 1.2em;
  }
  p {
    margin-top: 20px;
    font-size: 16px;
    line-height: 1.5em;
  }
  a {
    @include index_a;
  }
  .wrap_boxes {
    display: flex;
    width: 100%;
    flex-direction: column;
    flex-wrap: wrap;
    @include breakpoint($pad) {
      flex-direction: row;
      justify-content: space-between;
    }
  }
  .wrap_box {
    margin-bottom: 18px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: 100%;
    @include breakpoint($pad) {
      width: 49%;
    }
    @include breakpoint($pc) {
      width: 32%;
    }
  }
}

.p1,
.p8,
.p9,
.p11,
.p13 {
  h3 {
    margin-bottom: 15px;
    font-size: 30px;
    line-height: 1.2;
  }
  img {
    margin-bottom: 15px;
    display: block;
    width: 100%;
    height: auto;
  }
  p {
    margin-bottom: 15px;
    font-size: 18px;
    line-height: 1.667;
  }
  .wrap_boxes {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: space-between;
    @include breakpoint($pad) {
      flex-direction: row;
    }
  }
  .wrap_box {
    margin-bottom: 1em;
    display: flex;
    font-size: 18px;
    color: rgb(13, 95, 146);
    line-height: 1.333;
    @include breakpoint($pad) {
      width: 33%;
    }
/*     &:before {
  content: '>';
  margin-right: .3em;
} */
  }
}

.p2 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  h2 {
    text-align: center;
    @include breakpoint($pc) {
      text-align: left;
    }
  }
  .wrap_boxes {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    @include breakpoint($pc) {
      flex-direction: row;
      align-items: flex-start;
      justify-content: space-between;
    }
  }
  .wrap_box {
    margin-bottom: 28px;
    width: 100%;
    max-width: 320px;
    &:last-child {
      margin-bottom: 0;
    }
    @include breakpoint($pc) {
      margin-bottom: 0;
      width: 24%;
    }
  }
  img {
    display: block;
    width: 100%;
    height: auto;
  }
  h3 {
    padding-top: 14px;
    display: flex;
    flex-direction: column;
    font-size: 30px;
    line-height: 1.2em;
    font-weight: bold;
    @include breakpoint($pad) {
      flex-direction: row;
      justify-content: center;
      white-space: nowrap;
    }
    @include breakpoint($pc) {
      flex-direction: column;
      white-space: normal;
    }
  }
  p {
    padding-top: 14px;
    font-size: 18px;
    line-height: 1.667em;
  }
}


.p3,
.p4 {
  p {
    font-size: 18px;
    line-height: 1.66;
    margin-bottom: 1.5em;
    &:last-child {
      margin-bottom: 0;
    }
  }
}

.p4 {
  display: flex;
  flex-direction: column;
  h2 {
    @include breakpoint($pad) {
      text-align: center;
    }
  }
  img {
    margin: 0 auto;
    display: block;
    width: 214px;
    max-width: 100%;
    height: auto;
  }
  h3 {
    margin: 18px*1.5 0;
    font-size: 24px;
    line-height: 1.249em;
  }
}


.p5, .p6 {
  .wrap_boxes {
    display: flex;
    flex-direction: column;
    @include breakpoint($pc) {
      flex-direction: row;
      justify-content: space-between;
    }
  }
  .wrap_box {
    margin-top: 40px;
    @include breakpoint($pc) {
      width: 30%;
    }
  }
  p {
    line-height: 1.6em;
  }
  a {
    @include index_a;
  }
}


.p7 {
  .mobile {
    display: block;
    @include breakpoint($pc) {
      display: none;
    }
  }
  .pc {
    display: none;
    @include breakpoint($pc) {
      display: block;
    }
  }
  .wrap_boxes {
    display: flex;
    flex-direction: column;
    @include breakpoint($pc) {
      flex-direction: row;
      justify-content: space-between;
    }
  }
  .wrap_box {
    margin-bottom: 14px;
    margin-top: 0;
    width: 100%;
    @include breakpoint($pc) {
      width: 48%;
      font-size: 18px;
    }
  }
}

.p9 {
  .wrap_boxes {
    @include breakpoint($pad) {
      flex-direction: row;
      justify-content: flex-start;
      flex-wrap: wrap;
    }
    @include breakpoint($pc) {
      justify-content: space-between;
      flex-wrap: nowrap;
    }
  }
  .wrap_box {
    @include breakpoint($pad) {
      width: 33%;
    }
    @include breakpoint($pc) {
      width: auto;
    }
  }
}

.p10 {
  padding-top: 40px;
}

.p11 {
  .wrap_box {
    &:before {
      content: '✔';
      color: rgb(13, 95, 146);
    }
  }
}

.p12,
.p13,
.p14 {
  p {
    margin-top: 14px;
  }
  .pc {
    display: none;
    @include breakpoint($pc) {
      display: block;
    }
  }
  .pad {
    display: none;
    @include breakpoint($pad) {
      display: block;
    }
    @include breakpoint($pc) {
      display: none;
    }
  }
  .mobile {
    @include breakpoint($pad) {
      display: none;
    }
  }
}

.p13 {
  .wrap_boxes {
    @include breakpoint($pad) {
      flex-direction: row;
      justify-content: flex-start;
    }
  }
  .wrap_box {
  }
}

.p14 {
  h3 {
    font-size: 48px;
    line-height: 1.2em;
    @include breakpoint($pad) {
      margin-top: 29px;
    }
    @include breakpoint($pc) {
      margin-top: 1em;
      font-size: 42px;
      white-space: nowrap;
    }
  }
  .contact-box {
    padding-top: 20px;
    padding-bottom: 300px;
    background-image: url(../images/upload/qr.png);
    background-repeat: no-repeat;
    background-position: 50% 100%;
    background-size: 250px auto;
    @include breakpoint($pad) {
      padding-bottom: 100px;
      background-position: 100% 100%;
    }
    @include breakpoint($pc) {
      padding-top: 30px;
      padding-bottom: 30px;
      background-position: 100% 50%;
      background-size: 200px auto;
    }
    aside {
      margin: 8px 0;
      display: flex;
      flex-direction: row;
      justify-content: flex-start;
      align-items: flex-start;
      a {
        padding-top: 8px;
        font-size: 24px;
      }
      .aside_icon {
        margin-right: 8px;
        width: 40px;
        height: 40px;
        background-color: transparent;
        background-position: 50% 50%;
        background-repeat: no-repeat;
        background-size: contain;
        @for $i from 0 through 4 {
          &.icon#{$i} {
            background-image: url(../images/upload/icon#{$i}.png);
          }
        }
      }
      img {
        width: 40px;
        height: auto;
      }
    }
  }
}
