.page-header {
  position: relative;
  padding-top: em(75);
  display: block;
  width: 100%;
}

.header__start {
  position: fixed;
  top: 0;
  left: 0;
  z-index: index($zIndex, header__start);
  display: flex;
  align-items: center;
  justify-content: space-between;;
  height: em(75);
  width: 100%;
  max-width: $desktop;
  padding: 0 em(16);
  background-color: $white;
  &.js--active {
    /* position: absolute; */
  }
  @include breakpoint($pc) {
    left: 50%;
    transform: translateX(-50%);
  }
}

.header__logo {
  display: block;
  width: image-width('../images/layout/logo_mobile.png');
  height: image-height('../images/layout/logo_mobile.png');
  background: url('../images/layout/logo_mobile.png') 50% 50% / 100% auto no-repeat;
  @include hide-text;
}


.header__end {
  position: absolute;
  top: em(75);
  left: 0;
  z-index: index($zIndex, header__end);
  display: none;
  width: 100%;
  padding: 0 em(16);
  background-color: transparent;
  &.js--active {
    display: block;
  }
  @include breakpoint($pc) {
    position: fixed;
    top: 0;
    left: auto;
    right: 0;
    z-index: index($zIndex, header__box_pc);
    display: flex;
    flex-direction: row;
    align-items: center;
    width: auto;
    height: em(75);
    &.js--active {
      display: flex;
    }
  }
  @include breakpoint($desktop) {
    right: calc( (100% - 1080px) / 2 );
  }
}

.header__box {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  border: rgb(222, 222, 222) solid 1px;
  background-color: rgb(255, 255, 255);
  box-shadow: 0px 5px 5px 0px rgba(34, 34, 34, 0.5);
  @include breakpoint($pad) {
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
  }
  @include breakpoint($pc) {
    align-items: center;
    background-color: transparent;
    border: 0;
    box-shadow: none;
  }
}

.header__menu {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
}

.header--pad {
  display: none;
  @include breakpoint($pad) {
    display: block;
  }
  &.header--pad--no-pc {
    @include breakpoint($pc) {
      display: none;
    }
  }
}

.header__flex {
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
}

.header--mobile {
  @include breakpoint($pad) {
    display: none;
  }
  &.header--mobile--but-pc {
    @include breakpoint($pc) {
      display: block;
    }
  }
}



