* {
  box-sizing: border-box;
  @include selectColor( lighten($bgc, 10), $reverse_bgc );
}

html,
body {
  font-family: $fonts;
  width: 100%;
  height: 100%;
}

body {
  background-color: #f5f5f5;
  color: $reverse_bgc;
  text-rendering: optimizeLegibility;
  font-size: $fontSize_mobile;
}

a,
button,
input {
  appearance: none;
  border: 0;
  outline: 0;
}

a,
button {
  @include noSelect;
  color: currentColor;
  cursor: pointer;
}

a {
  text-decoration: none;
  span ~ &:first-of-type {
    margin-left: em(12);
  }
  &:hover {
    color: darken($color0, 45);
  }
  .ui.steps & {
    text-decoration: none;
  }
}


b,
strong {
  font-weight: bold;
}
