.header__box__group {
  position: relative;
  display: block;
  padding: 1em;
  /* border-top: rgb(222, 222, 222) solid 1px; */
  font-size: em(18);
  font-weight: bold;
  @include breakpoint($pad) {
    &:nth-child(1),
    &:nth-child(2),
    &:nth-child(3) {
      border-right: rgb(222, 222, 222) solid 1px;
    }
  }
  @include breakpoint($pc) {
    padding: 0;
    &:nth-child(1),
    &:nth-child(2),
    &:nth-child(3) {
      border-right: 0;
    }
  }
  hr {
    position: absolute;
    left: 0;
    bottom: 0;
    margin: 0;
    width: 100%;
    height: 0;
    border: rgb(222, 222, 222) solid 1px;
    @include breakpoint($pad) {
      display: none;
    }
  }
  &:last-child {
    hr {
      display: none;
    }
  }
  .h2, li, a {
    display: block;
  }
  .h2 {
    @include breakpoint($pc) {
      position: relative;
      margin-right: .3em;
      padding: .667em .778em;
      font-size: em(18);
      background-color: transparent;
      &.js--active {
        background-color: #c52525;
        color: $white;
      }
      &.lang {
        font-size: em(14);
        font-weight: normal;
        padding-right: 1.2em;
        background-image: url(../images/layout/arrow_down_lang.png);
        background-repeat: no-repeat;
        background-position: 100% 50%;
        &.js--active {
          background-color: transparent;
          color: $black;
          background-image: url(../images/layout/arrow_top_lang.png);
        }
      }
    }
  }
  ul {
    @include breakpoint($pc) {
      position: absolute;
      left: 0;
      display: none;
      background-color: $white;
      width: 10em;
      padding: 1em .8em;
      border: rgb(222, 222, 222) solid 1px;
      box-shadow: 0px 5px 5px 0px rgba(34, 34, 34, 0.5);
      &.lang {
        left: auto;
        right: 0;
      }
      &.js--active {
        display: block;
      }
    }
  }
  li {
    margin-top: 1em;
    @include breakpoint($pc) {
      &:first-child {
        margin-top: 0;
      }
    }
    a {
      position: relative;
      display: block;
      padding-left: 1.5em;
      line-height: 1.6em;
      color: #0d5f92;
      transition: color .3s;
      &:before {
        content: "﹀ ";
        position: absolute;
        top: 0;
        left: 0;
        display: block;
      }
      &.js--active {
        &:before {
          /* content: "︿ "; */
          content: "";
        }
      }
    }
    &:hover {
      a {
        color: lighten(#0d5f92, 40);
      }
    }
  }
}
