.menu-btn__hamburger {
  margin-left: .5em;
  display: flex;
  align-items: center;
  justify-content: flex-end;;
  text-decoration: none;
  @include breakpoint($pc) {
    display: none;
  }
}

.hamburger__legend {
  font-weight: bold;
  line-height: 1.4em;
}

.hamburger__nodes {
  position: relative;
  margin-right: em(9);
  display: block;
  width: em(24);
  height: em(19);
  &:before, &:after {
    content: '';
  }
  span, &:before, &:after {
    position: absolute;
    left: 0;
    display: block;
    width: 100%;
    height: em(3);
    background-color: $black;
    transition: all .3s;
  }
  span {
    top: 50%;
    margin-top: em(3) * -.5;
  }
  &:before {
    top: 0;
  }
  &:after {
    top: 100%;
    margin-top: em(3) * -.5;
  }
  &.js--active {
    span {
      opacity: 0;
    }
    &:before, &:after {
      top: 50%;
      margin-top: em(3) * -.5;
    }
    &:before {
      transform: rotate(-45deg);
    }
    &:after {
      transform: rotate(45deg);
    }
  }
}
